    if (dom) {
      const domWidth = dom.getBoundingClientRect().width;
      const parentNode = dom.parentNode;
      const parentNodeWidth = parentNode.getBoundingClientRect().width;
      dom.style.zoom = parentNodeWidth / domWidth * 0.9;
    }
